/*
* 经营看板-资金store
*/
import {observable, action} from 'mobx';
import Config from '@/config';
import {requestPost} from "@/utils/api";
import Schemas from "@/utils/Schemas/wisdomsee";


class CapitalStore {
   @observable capitalData = []; // 资金数据
   @observable lastUpdatedTime = "";

	// 根据分类查询报表
	@action
	getReportByClassifyId(param) {
		return requestPost('getReportByClassifyId', Config.wisdomsee.getReportByClassifyId, Schemas.wisdomsee.getReportByClassifyId, param)
	}

	// 看板分类查询
	@action
	getReportClassifys (param) {
		return  requestPost('getReportClassifys', Config.wisdomsee.getReportClassifys, Schemas.wisdomsee.getReportClassifys,
			param)
	}


}


export default CapitalStore;



